*{
  margin: 0;
  padding: 0;
  outline: none!important;
  box-sizing: border-box;
  text-decoration: none!important;
  
}
.nav1{
  display: flex;
  justify-content: space-between;
  padding: 25px 60px;
  background-color: rgb(20,25,46);
  align-items: center;
  position: static;
  border-bottom: 1px solid whitesmoke;
  
}
.nav1 .logo{
  width: 10%;
  
}
.nav1 .logo h2{
  color: white;
  font-size: 30px;
}

.nav1 .menulist{
  width: 90%;
}
.nav1 .exploreinput{
  display: flex;
  align-items: center;
  margin-top: 0px;
  border: 3px solid #EC892C;
  justify-content: space-between;
  border-radius: 999px;
  padding-left: 20px;
 
}
.nav1 .exploreinput input{
  background-color: transparent;
  border: none;
  outline: none;
  color: white;
}
.nav1 .exploreinput ::placeholder{
  color: white;
  font-size: 16px;
  letter-spacing: 1px;
  font-weight: normal;
  opacity: 0.8;
}

.nav1 .exploreinput svg{
  font-size: 30px;
  color: wheat;
}
.nav1 .exploreinput .sicon{
  background-color: #EC892C;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}
.nav1 ol{
  list-style: none;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.nav1 ol li{
  font-size: 20px;
  color: white;
}
.assetdetail .astdet{
  display: flex;
  flex-direction: column;
  border: none;
  margin: 20px 0;
}
.assetdetail .astdet h3{
  margin: 0;
  font-size: 25px;
}
.nav1 ol li a{
  color: white;
}
.nav1 .menulist .search{
  padding: 5px;
  background-color: white;
  width: 300px;
  border-radius: 20px;
}
.nav1 .menulist .search input{
  width: 270px;
  padding: 2px;
  border: none;
  outline: none;
  background-color: transparent;
}
.nav1 .menu{
  display: none;
}
.nav1 .menu svg{
  font-size: 30px;
  color: white;
}

.searchone{
  background-color: rgb(20,25,46);
  min-height: 100vh;
  width: 100%;
  overflow-x: hidden;
}
