@media screen and (max-width: 1180px) {
  .nav1 .menulist{
    
    background-color: rgb(20,25,46);
    width: 100%;
  }
  .nav1 .menulist ol{
    display: flex;
    flex-direction: column;
    position: absolute;
    background-color: rgb(20,25,46);
    padding: 30px 0;
    width: 100%;
    margin: 0;
    top:-1000px;
    left: 0;
    align-items: center;
    justify-content: space-evenly;
    align-content: center;
    text-align: center;
    height: 100vh;
    z-index: 5;
    transition: 0.9s all;
  }
  .nav1 .menusub ol{
    top:0
  }
  .nav1 .menu {
    display: block;
    position: relative;
    z-index: 6;
  }
  .menu .mbtn{
    display: block;
  }
  .menu .cbtn{
    display: none;
  }
  .rbtn .mbtn{
    display: none;
  }
  .rbtn .cbtn{
    display: block;
  }
}